#chat-main-container {
    @extend %flex;
    flex-flow: column;
    width: 100%;
    height: 38%;
    background-color: #333;
    position: absolute;
}
#chat-container {
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
    background-color: #1d1d1d;
    width: inherit;
    height: 80%;
    overflow-x: auto;
    padding: 0.5em;
}
#form {
    background: rgba(0, 0, 0, 0.15);
    padding: 0.25rem;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    height: 3rem;
    box-sizing: border-box;
    backdrop-filter: blur(10px);
}
#input {
    border: none;
    padding: 0 1rem;
    flex-grow: 1;
    border-radius: 2rem;
    margin: 0.25rem;
}
#input:focus {
    outline: none;
}
#form > button {
    background: #333;
    border: none;
    padding: 0 1rem;
    margin: 0.25rem;
    border-radius: 3px;
    outline: none;
    color: #fff;
}
#messages {
    list-style-type: none;
    margin: 1em 0 0 3em;
    padding: 0;
}
#messages > li {
    padding: 0.7rem 1rem;
    width: 100%;
    color: bisque;
    display: flex;
}
